:root {
  --primary-color: #1756f8;
  --secondary-color: #70cbe8;
  --accent-color: #b5f6fa;
  --bg-color: #95edf2;
  --white: #fff9f9;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

body {
  min-height: 100dvh;
  background-color: var(--bg-color);
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
}

header {
  width: 100%;
  border-bottom: 3px solid var(--white);
  background-color: var(--secondary-color);
  z-index: 9999;
  border-radius: 0 0 0 3rem;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  display: flex;
  position: fixed;
}

.logo a {
  color: var(--white);
  padding: 1rem;
  font-size: 1.75rem;
  font-weight: 700;
  text-decoration: none;
}

header div a {
  color: var(--white);
  margin: 1rem;
  font-size: 1.25rem;
  font-weight: 500;
  text-decoration: none;
}

@media (width <= 600px) {
  header {
    flex-direction: column;
    align-items: flex-start;
  }

  .logo {
    margin-bottom: 10px;
    font-size: 20px;
  }

  div {
    flex-direction: column;
    gap: 5px;
  }
}

main {
  min-width: 100%;
  max-width: 1500px;
  flex-grow: 1;
  margin-top: 6rem;
  position: relative;
}

.hero {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  margin-top: 4rem;
  padding-inline: 1rem;
  display: flex;
}

.hero h2 {
  font-size: clamp(2rem, 4vw + 1rem, 3rem);
}

.hero h3 {
  font-size: clamp(1.5rem, 2vw + 1rem, 2rem);
}

.hero a {
  color: var(--white);
  background-color: var(--primary-color);
  border: none;
  border-radius: 0 0 0 1rem;
  padding: .5rem 3rem;
  font-size: 1.25rem;
  font-weight: 700;
  text-decoration: none;
}

.hero p {
  width: 40%;
  font-size: 1rem;
}

.svg-container {
  width: 100%;
  z-index: -1;
  opacity: .8;
  justify-content: center;
  align-items: baseline;
  display: flex;
  position: absolute;
  bottom: 0;
}

.svg-container :nth-child(2) {
  display: none;
}

svg {
  max-width: 100%;
}

@media screen and (width >= 600px) {
  .svg-container {
    justify-content: space-between;
    padding-inline: 2rem;
  }

  .svg-container :nth-child(2) {
    display: block;
  }
}

footer {
  width: 100%;
  background-color: var(--secondary-color);
  border-top: 3px solid var(--white);
  border-radius: 0 1.75rem 0 0;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  display: flex;
}

footer h4 {
  color: var(--white);
  font-size: 1.5rem;
}

footer div {
  width: 25%;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.fa {
  background-color: var(--accent-color);
  color: var(--secondary-color);
  text-align: center;
  border-radius: 50%;
  padding: 5%;
  text-decoration: none;
}

.fa:hover {
  color: var(--white);
  background-color: #1756f8;
}

.choose-form {
  width: 100%;
}

.choose-form button {
  color: var(--white);
  background-color: var(--primary-color);
  border: none;
  margin-top: 4rem;
  padding: .8rem 3rem;
  font-size: 1.25rem;
  font-weight: 700;
  text-decoration: none;
}

.choose-cards-container {
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  display: flex;
}

.card {
  max-width: 12rem;
  aspect-ratio: 1;
  background-color: var(--accent-color);
  flex-direction: column;
  flex-basis: 100%;
  justify-content: space-evenly;
  align-items: center;
  margin: 1rem;
  display: flex;
}

@media screen and (width >= 300px) {
  .choose-cards-container {
    gap: 0;
  }

  .card {
    flex-basis: calc(50% - 2rem);
  }
}

.hide {
  display: none;
}

